/* Scope the styles only to the Technologies component */
#technologies .wrapper {
  padding: 2rem;
  background-color: #1a1a1a;
  color: #ffffff;
}


#technologies h2 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
  text-align: center;
}

#technologies h3 {
    text-align: center;
  font-size: 1.75rem;
  margin: 2rem 0 1rem;
}

#technologies .technologies-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 3rem;
}

#technologies .technology-section {
  flex: 1 1 45%;
  min-width: 300px;
}

#technologies .technologies-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
  gap: 1.5rem;
  justify-items: center;
  align-items: center;
}

#technologies .tech-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  transition: transform 0.2s ease-in-out;
}

#technologies .tech-item img {
  width: 50px;
  height: 50px;
  margin-bottom: 0.5rem;
}

#technologies .tech-item i {
  font-size: 3rem;
  margin-bottom: 0.5rem;
}

#technologies .tech-item p {
  font-size: 0.9rem;
}

#technologies .tech-item:hover {
  transform: scale(1.1);
}
